
import tabGroup from '@/components/group/tab'
import Timeline from 'vux/src/components/timeline/timeline.vue'
import TimelineItem from 'vux/src/components/timeline/timeline-item.vue'
import Toast from 'vux/src/components/toast/index.vue'

import {getLogisticsList} from '@/api/logistics'
export default {
components: {
tabGroup: tabGroup,
Timeline,
TimelineItem
},
data () {
return {
title: '查看物流',
wuliu:true,
orderNum:2569463353535353,
expressCompany:'',
expressCode:'',
expressRecode:'',
companyName:'',
defaultLogisticsCompanies:[
{
label: '顺丰速运',
value: 'SF'
},
{
label: '百世快递',
value: 'HTKY'
},
{
label: '中通快递',
value: 'ZTO'
},
{
label: '申通快递',
value: 'STO'
},
{
label: '圆通速递',
value: 'YTO'
},
{
label: '韵达速递',
value: 'YD'
},
{
label: '邮政快递包裹',
value: 'YZPY'
},
{
label: 'EMS',
value: 'EMS'
},
{
label: '天天快递',
value: 'HHTT'
},
{
label: '京东快递',
value: 'JD'
},
{
label: '优速快递',
value: 'YC'
},
{
label: '申通快递',
value: 'STO'
},
{
label: '申通快递',
value: 'STO'
}
]
}
},
props: {},
created(){
this.expressCode=this.$route.query.expressCode;
this.expressCompany=this.$route.query.expressCompany;
this.companyName=this.defaultLogisticsCompanies.filter(item=>{
return this.expressCompany==item.value
})
},
mounted(){
this.getLogisticsList();
},
methods: {
copyNum(){
let orderNum=this.$refs.waitcopy;
console.log(orderNum);
orderNum.select();
document.execCommand("Copy");
this.$vux.toast.text('复制成功')
},
getLogisticsList(){
getLogisticsList({expressCode:this.expressCode,expressCompanry:this.expressCompany,pageNum:1,pageSize:1})
.then(res=>{
console.log(res.data[0].logisticsTrace);

this.expressRecode=JSON.parse(res.data[0].logisticsTrace);

})
}
}
}



